















import { Component, Vue, Emit } from "vue-property-decorator";

@Component
export default class General extends Vue {
    msg: string | null = null;
    get hasErrors() {
        return {
            "md-invalid": this.msg!?.trim().length <= 0,
        };
    }
    send() {
        this.msg = this.msg === null ? "" : this.msg.trim();
        if (this.msg) {
            this.MessageSent();
        }
    }
    @Emit()
    MessageSent() {
        return this.msg;
    }
}
